/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The Two Most Powerful Women On The Face Of The Earth","date":"06 Dec 2005","slug":"/2005-12-06-the-two-most-powerful-women-on-the-face-of-the-earth/","tags":"Y2005"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/12/merkelrice.jpg\" border=\"0\" height=\"287\" width=\"420\" alt=\"merkelrice.jpg\" /></p>\n<p>In the middle must be <a href=\"http://clinton.senate.gov/\">Hillary's</a> shadow... (photo from <a href=\"http://www.spiegel.de/politik/deutschland/0,1518,grossbild-551176-388942,00.html\">Spiegel Online</a>)</p>"},"next":{"fields":{"title":"Nikolaus Report","date":"06 Dec 2005","slug":"/2005-12-06-nikolaus-report/"}},"previous":{"fields":{"title":"Chancellor Redirected","date":"07 Dec 2005","slug":"/2005-12-07-chancellor-redirected/"}}},{"node":{"fields":{"title":"Nikolaus Report","date":"06 Dec 2005","slug":"/2005-12-06-nikolaus-report/","tags":"Y2005"},"html":"<p>Since all the <a href=\"http://justcallmemausi.blogspot.com/2005/12/nikolaus-was-here.html\">expat</a> <a href=\"http://greenhaddock2.blogspot.com/2005/12/der-nikolaus-ist-da.html\">blogs</a> <a href=\"http://www.inactualfact.com/?p=190\">seem</a> to be reporting on Nikolaus activities today, here's a quick recap from our house. For us the Nikolaus is a messenger boy for Santa Claus, so we have Christopher prepare his wish list (this year consisting of the entire Lego Bionicle catalog) for him to deliver to the big man. Christopher does have an American <a href=\"http://www.papascott.de/archives/2003/12/06/christmas-stocking/\" title=\"PapaScott &raquo; Blog Archive &raquo; Christmas Stocking\"> Christmas </a><a href=\"http://www.papascott.de/archives/2004/12/05/american-nikolaus/\" title=\"PapaScott &raquo; Blog Archive &raquo; American Nikolaus\">stocking</a> he uses instead of a boot, but this year the <a href=\"http://www.biermoesl-blosn.de/polt/nikolausi.htm\">Nikolausi</a> was lazy and left a white Bionicle on the patio in the rain. Christopher was up at quarter-to-seven to pick up the loot, no photographers were awake enough to capture the event.</p>"},"next":{"fields":{"title":"'ssh' verboten","date":"05 Dec 2005","slug":"/2005-12-05-ssh-verboten/"}},"previous":{"fields":{"title":"The Two Most Powerful Women On The Face Of The Earth","date":"06 Dec 2005","slug":"/2005-12-06-the-two-most-powerful-women-on-the-face-of-the-earth/"}}},{"node":{"fields":{"title":"'ssh' verboten","date":"05 Dec 2005","slug":"/2005-12-05-ssh-verboten/","tags":"Y2005"},"html":"<p>Funny, when I first tried to publish the VPN post yesterday, I kept getting the strange error 'Precondition Failed'. PapaTech is hosted at <a href=\"http://textdrive.com/\">TextDrive</a>, and it turns out they do some very aggresive filtering with mod_security, including the names of certain Unix commands like 'ssh', 'wget', and 'echo' which can be used in HTTP requests to run commands on unsecure servers. The side-effect is it's not possible to mention 'ssh' (without quotes)  on a site hosted at TextDrive.</p>\n<p>Actually, at TextDrive you can <a href=\"http://kb.textdrive.com/article/disabling-mod_security\" title=\"TextDrive Knowledge Base: Disabling mod_security\">disable mod_security in your .htaccess file</a>, or even modify the stock ruleset, but it doesn't work if you proxy lighttpd in front of their Apache, like PapaTech does.</p>"},"next":{"fields":{"title":"Cats in Sinks","date":"05 Dec 2005","slug":"/2005-12-05-cats-in-sinks/"}},"previous":{"fields":{"title":"Nikolaus Report","date":"06 Dec 2005","slug":"/2005-12-06-nikolaus-report/"}}}],"pathPrefix":"","first":false,"last":false,"index":260,"pageCount":1101}}

/***/ })

